import { AccountService } from '@core/services/account/account.service';
import { Component, OnInit, OnDestroy, ViewChild, TemplateRef, Inject } from '@angular/core';
import { TransferService } from '@core/services/transfer/transfer.service';
import { Subscription, combineLatest } from 'rxjs';
import { SimulateOutput, Transfer } from '@api/models';
import { ErrorHandlerService } from '@core/services/errors/error-handler.service';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { parseLocaleNumber } from '@shared/directives/parse-currency.directive';
import { HeaderService } from '@core/services/header/header.service';
import { GlobalPositionService } from '@core/services/global-position/global-position.service';
import { map, switchMap } from 'rxjs/operators';
import { findAccount, isInvalidOtp, findDeposit } from '@core/utils';
import { Router, ActivatedRoute } from '@angular/router';
import { BANNERS, Banners } from '@core/constants/banners.constants';

@Component({
  selector: 'app-transfer',
  templateUrl: './transfer.component.html',
  styles: [],
})
export class TransferComponent implements OnInit, OnDestroy {
  state$ = this.createState();

  @ViewChild('otpModal') otpModal: TemplateRef<any>;
  simulateOutput: SimulateOutput;
  invalidOtp = false;
  currentTransfer: Transfer;

  private referenceOTP: string;
  private modalRef: BsModalRef;
  private subs = new Subscription();

  constructor(
    private headerService: HeaderService,
    private transferService: TransferService,
    private accountService: AccountService,
    private errorHandler: ErrorHandlerService,
    private modalService: BsModalService,
    private globalPositionService: GlobalPositionService,
    private router: Router,
    private route: ActivatedRoute,
    @Inject(BANNERS) public readonly banners: Banners
  ) {}

  ngOnInit() {
    this.globalPositionService.fetchGlobalPosition();
    this.headerService.updateTitle('transfer_title');
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
    this.closeModal();
  }

  openModal(ref: any) {
    if (!this.modalService.getModalsCount()) {
      this.modalRef = this.modalService.show(ref);
    }
  }

  closeModal() {
    this.modalRef?.hide();
  }

  generateOtp() {
    this.invalidOtp = false;
    this.referenceOTP = null;

    this.subs.add(
      this.transferService.otp(this.currentTransfer).subscribe(
        ({ referenceOTP }) => {
          this.referenceOTP = referenceOTP;
          this.openModal(this.otpModal);
        },
        (error) => {
          this.errorHandler.showError(error);
        }
      )
    );
  }

  simulateTranfer({ amount, concept }) {
    this.subs.add(
      this.state$
        .pipe(
          switchMap((state) => {
            this.currentTransfer = {
              amount: parseLocaleNumber(amount).toString(),
              concept,
              sender: { iban: state?.account?.IBAN, name: state?.customerName },
              receiver: { iban: state?.globalPosition?.OriginIBAN, name: state?.customerName },
            };

            return this.transferService.simulate(this.currentTransfer);
          })
        )
        .subscribe(
          (result) => {
            this.simulateOutput = result;
          },
          (error) => {
            this.errorHandler.showError(error);
          }
        )
    );
  }

  makeTransfer(keyOTP: string) {
    this.subs.add(
      this.transferService
        .confirm({ ...this.currentTransfer, otp: { keyOTP, referenceOTP: this.referenceOTP } })
        .subscribe(
          () => {
            this.navigateTouSuccessPage();
            this.closeModal();
          },
          (error) => {
            if (isInvalidOtp(error)) {
              this.invalidOtp = true;
              return;
            }
            this.closeModal();
            this.errorHandler.showError(error);
          }
        )
    );
  }

  clearCurrentTranfer() {
    this.currentTransfer = null;
    this.simulateOutput = null;
  }

  private getCustomerName() {
    return this.accountService.currentCustomer$.pipe(
      map((result) =>
        result?.DatosPersonales
          ? `${result.DatosPersonales?.nombreCliente} ` +
            `${result.DatosPersonales?.apellido1Cliente} ` +
            `${result.DatosPersonales?.apellido2Cliente}`
          : ''
      )
    );
  }

  private createState() {
    return combineLatest([
      this.globalPositionService.globalPosition$,
      this.globalPositionService.globalPosition$.pipe(findAccount()),
      this.globalPositionService.globalPosition$.pipe(findDeposit()),
      this.getCustomerName(),
    ]).pipe(
      map(([globalPosition, account, deposit, customerName]) => ({
        globalPosition,
        account,
        deposit,
        customerName,
      }))
    );
  }

  private navigateTouSuccessPage() {
    this.router.navigate(['./transfer-success'], {
      skipLocationChange: true,
      relativeTo: this.route,
    });
  }
}
