import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Transfer, SimulateOutput, TransferOutput, OtpOutput } from '@api/models';
import { environment } from '@environment/environment';

@Injectable({
  providedIn: 'root',
})
export class TransferService {
  constructor(private http: HttpClient) {}

  simulate(transfer: Transfer) {
    const url = `${environment.apiBasePath}${environment.endpoints.transfer.simulate}`;
    return this.http.post<SimulateOutput>(url, transfer);
  }

  otp(transfer: Transfer) {
    const url = `${environment.apiBasePath}${environment.endpoints.transfer.otp}`;
    return this.http.post<{ referenceOTP: string }>(url, transfer);
  }

  confirm(transfer: Transfer) {
    const url = `${environment.apiBasePath}${environment.endpoints.transfer.confirm}`;
    return this.http.post<TransferOutput>(url, transfer);
  }
}
