import { AbstractControl } from '@angular/forms';
import { parseLocaleNumber } from '@shared/directives/parse-currency.directive';

export function balanceValidator(balance: number) {
  return (control: AbstractControl) => {
    const amount = parseLocaleNumber(control?.value);
    return amount > balance ? { balanceError: true } : null;
  };
}

export function amountValidator(control: AbstractControl) {
  const amount = parseLocaleNumber(control.value);

  return isNaN(amount) ? { invalidAmount: true } : amount <= 0 ? { minAmountError: true } : null;
}
