import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { balanceValidator, amountValidator } from '@transfer/utils/transfer.validators';

@Component({
  selector: 'app-transfer-form',
  templateUrl: './transfer-form.component.html',
  styles: [],
})
export class TransferFormComponent implements OnInit {
  @Input() senderIban: string;
  @Input() receiverIban: string;
  @Input() balance: number;
  @Output() submitForm = new EventEmitter<{ amount: string; concept: string }>();
  form: FormGroup;
  submitted = false;

  get hasAmountError() {
    const amountControl = this.form.get('amount');
    return this.hasError(amountControl) && amountControl.hasError('balanceError');
  }

  get hasMinAmountError() {
    const amountControl = this.form.get('amount');
    return this.hasError(amountControl) && amountControl.hasError('minAmountError');
  }

  constructor(private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.createForm();
  }

  onSubmit() {
    this.submitted = true;
    this.form.markAllAsTouched();

    if (this.form.valid) {
      const amount: string = this.form.value.amount;
      const concept: string = this.form.value.concept;

      this.submitForm.emit({ amount, concept });
    }
  }

  hasError(control: AbstractControl) {
    return control.touched && control.invalid;
  }

  private createForm() {
    this.form = this.formBuilder.group({
      amount: ['', [Validators.required, balanceValidator(this.balance), amountValidator]],
      concept: ['', Validators.required],
    });
  }
}
